import React, { Component } from 'react';
import PropTypes from 'prop-types';
import $ from 'jquery';

import '../styles/MenuContents.scss';

import '../styles/prettyHr.scss';

class MenuContents extends Component {
  static propTypes = {
    data: PropTypes.object.isRequired,
    isTouch: PropTypes.bool.isRequired
  };
  componentDidMount() {
    const menuHeader = document.getElementsByClassName('menuHeader');

    // const scrollTo = this.props.isTouch
    //   ? menuHeader[0].clientHeight - 110
    //   : menuHeader[0].clientHeight - 110;
    const scrollTo = menuHeader[0].clientHeight - 120;
    if (this.props.fixed) {
      $('.wrapper').animate({ scrollTop: scrollTo }, 0);
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    return false;
  }
  render() {
    const { data } = this.props;
    // console.log('menucontent');
    return (
      <div className="menuContentBox">
        <h1 className="title">{data.title}</h1>
        {/* <hr className="prettyHr" /> */}
        <div
          style={{
            background: 'url("/images/rose-hr-260.jpg")',
            backgroundSize: 'cover',
            width: '260px',
            height: '31px',
            margin: '0 auto'
          }}
        />
        <div className="itemsBox">
          <div className="itemBoxGrid">
            {data.data.map(item => (
              <div className="itemBox" key={item.id}>
                <div className="imageBox">
                  <img src={item.image} alt={item.name} title={item.name} />
                </div>

                <div className="contentBox">
                  <div className="name">
                    {item.name}
                    <span className="day">{item.day}</span>
                  </div>
                  <div className="description">{item.description}</div>
                  <div className="price">
                    {item.regularPrice && (
                      <span className="striked">{item.regularPrice}</span>
                    )}
                    &nbsp;$
                    {item.price}
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
        {/* <div>
          {Array.apply(null, Array(50)).map((item, i) => (
            <h1 key={i}>Test</h1>
          ))}
        </div> */}
      </div>
    );
  }
}

export default MenuContents;
